:root {
  --bg-color: rgb(17,17,17);
  --shadow-cursor: 255, 0, 58, 0.078;
  --header-height: 3rem;
  --font-semi: 600;
  --hover-button: rgba(255, 255, 255, 0.01);
  --accent-color: #ff003c;
  --progress--accent-color: #ff003c2c;
  --progress-correct--accent-color: #ff003c2c;
  --shadow-accent-color: #bd022d;
  --border-shadow-accent-color: #8c0322;
  --foreground-color--title: #fff;
  --foreground-color--section: rgb(254, 170, 170);
  --foreground-color-secondary: rgba(255, 213, 213, 0.5);
  --shadow--color-box: rgba(23, 23, 23, 1);
  --box-bg: rgba(20, 20, 20, 1);
  --progress-width: 0;
}

* {
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 700;
  box-sizing: border-box;
  color: #d3d3d3;
}

.fp-watermark{
  display: none !important;
}

.absolute{
  position: absolute;
  width: 100%;
  height: 100%;
}

.App {
  background-color: var(--bg-color);
  text-align: center;
  position: fixed;
  width: 100%;
  /* height: 100%; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

Canvas{
  position: absolute;
}

.innerin {
  width: 1440px;
  max-width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
  z-index: 101;
}

.contact_in{
  width: 100% !important;
  max-width: 100%;
  padding: 0 0 !important;
  margin: 0 auto;
  position: absolute;
  z-index: 101;
}

.hero {
  display: flex;
  height: 100vh;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.cursor-dot {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  opacity: 1;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.cursor-dot {
  width: 80vh;
  height: 80vh;
  z-index: 999999999999999;
  background: radial-gradient(circle at center, rgb(var(--shadow-cursor)), transparent 80%);
}

/* main */

.title__style {
  font-size: 1.9rem;
  font-family: "Source Sans Pro";
  font-weight: var(--font-semi);
  color: var(--foreground-color--title);
  text-decoration: none;
  margin-left: 2rem;
}


@media (min-width:768px) {
  .App-header {
    display: flex;
    height: 100vh;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
}

.hero .dots_decor {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  margin: auto;
  right: -10%;
  transform: rotate(90deg);
}

.hero .hero_cont {
  max-width: 500px;
}

@media (min-width: 768px) {
  .hero .hero_cont {
    width: 50%;
    max-width: 550px;
    margin: 0;
  }
}

@media (min-width: 1140px) {
  .hero .hero_cont h1 {
    font-size: 60px;
    line-height: 50px;
  }
}

.hero .hero_cont h1 {
  margin: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  font-size: 39px;
  line-height: 28px;
}

@media (min-width: 768px) {
  body .font-60 {
    font-size: 60px;
    line-height: 54px;
  }
}

@media (max-width: 1141px) {
  body .font-60 {
    font-size: 40px;
    line-height: 34px !important;
  }
}

body .font-60 {
  font-size: 30px;
  cursor: default;
  line-height: 60px;
}

body h1 {
  font-size: 36px;
  line-height: 42px;
}

.hero .hero_cont h1 div {
  margin: 0 15px 0 0 !important;
}

.hero .hero_cont h1 div {
  display: inline;
  margin: 15px 0 0;
}

@media (min-width: 350px) {
  .hero .hero_cont h1 div {
    margin: 0 15px 15px 0;
  }
}

.hero .hero_cont h1 div span {
  display: inline-block;
  transition: all .3s ease-out;
}

.grid--decor{
  position: absolute;
  width: 100%;
  height: 100%;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  grid-template-rows: repeat(8, 1fr);
  grid-template-columns: repeat(12, 1fr);
  grid-auto-columns: 1fr;
  display: grid;
  position: absolute;
  top: 0%;
  bottom: 0%;
  right: 0%;
  left: 0%;
  opacity: .4;
  -webkit-mask-image: radial-gradient(circle, transparent 50%, var(--bg-color) 0%);
  mask-image: radial-gradient(circle, transparent 50%, var(--bg-color) 0%);
  overflow: hidden;
  pointer-events: none;
  z-index: 1;
}

.grid_block{
  min-height: 100%;
  min-width: 100%;
  border: .2px solid rgba(82, 82, 82, .1);
  display: inline-block;
}

/* .grid_block:hover{
  background-color: rgba(255, 255, 255, .01);
} */

/* webkit scroll */

::-webkit-scrollbar {
  width: 3px;
  height: 10px;
  /* background-color: blue; */
  border-radius: 10vh;
  transition: all .5s cubic-bezier(0.215, 0.610, 0.355, 1);
}

::-webkit-scrollbar:hover{
  width: 7px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0);
  border-radius: 10vh;
}

::-webkit-scrollbar-track:hover{
  background: rgba(255, 255, 255, 0.01);
}

::-webkit-scrollbar-thumb {
  background: rgb(163, 163, 163);
  border-radius: 10vh;
}
