.about_section {
    align-items: center;
    padding: 100px 0;
  }

  .text__desc{
    color: aliceblue;
    opacity: 0.7;
    font-weight: 300;
    letter-spacing: 1px;
    font-size: 17px;
  }

  .container__proyects{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  .text__title {
    /* background: red; */
    width: 100%;
  }

  .proyects__wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 0;
    width: 100%;
    height: 330px;
    -webkit-mask-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, black 15%, black 100%);
    overflow: auto;
    transition: all 0.5s ease-in-out;
  }

  .btn__style {
    width: 100%;
    min-height: 193px;
    max-height: 193px;
    position: relative;
    border: 2px solid transparent;
    /* background-color: red; */
    margin-bottom: 15px;
    border-radius: 15px;
    transition: background .3s cubic-bezier(0.39, 0.575, 0.565, 1);
    overflow: hidden;
    transition: opacity 0.3s ease, height 0.3s ease-in-out, filter 1s ease-in-out;
  }

  .btn__style:hover {
    border-top: 2px solid var(--hover-button);
    background-color: var(--hover-button);
    filter: none;
  }

  .btn__style.expanded {
    height: 300px;
  }

  .proyects__wrapper:hover> .btn__style:not(:hover) {
    /* filter: blur(5px); */
    opacity: 0.5;
  }

  .btn__wrapper{
    display: flex;
    width: 100%;
    height: 100%;
  }

  .section--centered {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .section--left{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 25%;
    /* background-color: blue; */
    overflow: hidden;
  }

  .section--right{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 75%;
    /* background-color: green; */
    overflow: hidden;
  }

  .section--centered .container__img{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    /* margin-top: 20px; */
    height: 100%;
    overflow: hidden;
  }

  .section--centered .container__img img{
    width: 100%;
    height: 100%;
    border-radius: .5rem;
    border: 2px solid var(--shadow--color-box);
    filter: grayscale(0%);
    cursor: pointer;
    transition: all .5s ease-in-out;
    object-fit: cover;
  }

  .btn__style:hover .section--centered .container__img img{
    filter: brightness(0.7) grayscale(40%);
    transform: matrix(1.2, 0, 0, 1.2, 0, 0);
  }

  .expanded__title{
    position: absolute;
    opacity: 0;
    font-size: 1.2rem;
    transition: all 0.5s ease-in-out;
    pointer-events: none;
    text-decoration: dashed;
    text-transform: uppercase;
    /* -webkit-text-stroke: 2px silver; */
    /* color: transparent; */
    /* text-shadow: 0 0 10px #000000; */
    /* -webkit-text-fill-color: transparent; */
  }

  .btn__style:hover .section--centered .container__img .expanded__title{
    opacity: 1;
  }

  @media (min-width: 1140px) {
    .grid {
      grid-template-columns: repeat(2, 1fr) !important;
    }
  }

  .container__img{
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    width: 100%;
    /* margin-top: 20px; */
    height: 90%;
    overflow: hidden;
  }

  .container__img img{
    width: 90%;
    height: 100%;
    border-radius: .5rem;
    border: 2px solid var(--shadow--color-box);
    filter: grayscale(90%);
    cursor: pointer;
    transition: all .5s ease-in-out;
    object-fit: cover;
  }

  .container__img img.expanded {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .btn__style:hover img{
    filter: grayscale(0%);
  }

  .wrapper__content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    width: 90%;
    height: 90%;
    /* background-color: red; */
    padding: 0 10px;
    overflow: hidden;
  }

  .content__title{
    height: 18%;
    font-size: 23px;
    color: silver;
    font-weight: 500;
    margin-bottom: 5px;
    overflow: visible;
  }

  .icon{
    font-size: 17px;
    color: var(--shadow-accent-color);
    margin-left: 5px;
    overflow: visible;
    transition: transform .3s ease-in-out;
  }
  .icon:hover{
    transform: scale(1.1);
  }

  .content__state{
    height: 15%;
    font-size: 15px;
    color: gray;
    font-weight: 300;
    letter-spacing: 1px;
    margin-top: 5px;
    margin-bottom: 5px;
    overflow: hidden;
  }

  .content__desc{
    display: -webkit-box;
    /* height: 47%; */
    font-size: 13px;
    color: rgb(107, 107, 107);
    font-weight: 300;
    flex-wrap: wrap;
    letter-spacing: 1px;
    margin-top: 5px;
    margin-bottom: 5px;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }

  .content--tags{
    height: 20%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    /* background-color: red; */
    margin-top: 5px;
    margin-bottom: 5px;
    overflow: hidden;
  }

  .tag {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: .25rem;
    padding-bottom: .25rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    border-radius: 15px;
    margin-right: 7px;
    background-color: var(--shadow-accent-color);
    color: white;
    font-size: 13px;
    font-weight: 300;
    letter-spacing: 1px;
    transition: all .3s ease-in-out;
  }

  @media (min-width: 768px) {
    .about_section {
      display: flex;
      justify-content: space-between;
      padding: 200px 0;
    }
  }

  @media (max-width: 782px) {
    .about_section .font-60 {
      line-height: 34px !important;
    }
  }

  @media (min-width: 992px) {
    .about_section {
      padding: 0;
    }
  }

  .about_section .t {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: -1;
    margin: auto;
    opacity: 0.1;
    filter: drop-shadow(10px 10px 5px #000000);
  }

  @media (min-width: 768px) {
    .about_section .t {
      margin: auto auto auto 30%;
    }
  }

  .about_section .about_me {
    display: flex;
    flex-wrap: wrap;
    max-width: 400px;
    /* margin-bottom: auto; */
  }

  @media (min-width: 768px) {
    .about_section .about_me {
      width: 50%;
      max-width: 100%;
    }
  }

  .about_section .about_me .font-60 {
    margin: 0;
    font-size: 39px;
  }

  @media (min-width: 1140px) {
    .about_section .about_me .font-60 {
      font-size: 60px;
    }
    .about_section .about_me .font-30 {
      font-size: 30px !important;
    }
  }

  .about_section .about_me .font-60 span {
    display: inline-block;
    transition: all 0.3s ease-out;
  }

  .about_section .about_me .font-60 span:hover {
    color: var(--shadow-accent-color);
    animation: rubberBand;
    animation-duration: 1s;
  }

  .about_section .about_me .font-60 span.space {
    padding-right: 15px;
  }

  .about_section .about_me p {
    font-weight: 300;
    letter-spacing: 1px;
    color: aliceblue;
    opacity: 0.7;
    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: 768px) {
    .about_section .about_me p {
      padding-right: 2rem;
    }
  }

  @media (min-width: 992px) {
    .about_section .about_me p {
      font-size: 20px;
    }
  }

  @media (min-width: 768px) {
    .about_section .about_info {
      width: 50%;
    }
  }

  @media (min-width: 375px) {
    .about_section .about_info {
      text-align: left;
    }
  }

  @media (min-width: 992px) {
    .about_section .about_info {
      margin-right: 100px;
    }
  }

  .rubberBand {
    animation: rubberBand;
    animation-duration: 1s;
  }


  .container--imgZoom {
    position: absolute;
    display: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  .container--imgZoom img {
    width: auto;
    height: auto;
    object-fit: cover;
    border-radius: .5rem;
    border: 2px solid var(--shadow--color-box);
    /* filter: grayscale(90%); */
    cursor: pointer;
    transition: all .5s ease-in-out;

  }

  .container__title{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
  }

  .container__title button{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-color: transparent;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
    margin-left: 1vh;
    transition: all .3s ease-in-out;
  }

  .container__title button:hover {
    /* background-color: var(--shadow-accent-color); */
    transform: scale(1.1);
  }

  .container__title button:active {
    background-color: var(--shadow-accent-color);
    transform: scale(1.1);
  }

  .container__title button.selected {
    background-color: var(--shadow-accent-color);
    transform: scale(1.1);
  }

  .icon--view{
    font-size: 17px;
    /* color: var(--shadow-accent-color); */
    overflow: visible;
    transition: transform .3s ease-in-out;
  }

  .icon--view:hover{
    transform: scale(1.1);
  }


  .grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 1rem;
    /* padding: 1rem; */
    animation: 2.5s cubic-bezier(.25, 1, .30, 1) circle-in-hesitate both;
    /* animation: zoomIn .5s ease-in-out forwards; */
  }

  .detailed{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    animation: 2.5s cubic-bezier(.25, 1, .30, 1) wipe-in-right both;

  }

  .container--imgZoom img.zoomIn {
    animation: zoomIn .5s ease-in-out;
  }

  .container--imgZoom img.zoomOut {
    animation: zoomOut .5s ease-in-out;
  }

  @keyframes zoomIn {
    from {
      transform: scale(0);
    }
    to {
      transform: scale(1);
    }
  }

  @keyframes zoomOut {
    from {
      transform: scale(1);
    }
    to {
      transform: scale(0);
    }
  }



  @keyframes circle-in-hesitate {
    from {
      opacity: 0;
      transform: matrix3d(1, 0, 0, 0, 0, 1, 0, -0.0005, 0, 0, 1, 0, 0, 0, 0, 1)
    }
    to {
      opacity: 1;
      /* transform: translateX(0);
      clip-path: inset(0 0 0 0); */
    }
  }

  @keyframes wipe-in-right {


    from {
      opacity: 0;
      transform: matrix3d(1, 0, 0, 0, 0, 1, 0, -0.005, 0, 0, 1, 0, 0, 0, 0, 1);

      /* transform: translateX(-100%); */
      /* clip-path: inset(0 0 0 100%); */
    }
    to {
      opacity: 1;
      transform: translateX(0);
      clip-path: inset(0 0 0 0);
    }
  }